body{
  margin: 0;
  width: 100vw;
  box-sizing: border-box !important;
  overflow-x: hidden !important;
}
::-webkit-scrollbar{
  width: 7px
}
::-webkit-scrollbar-thumb{
  background-color: aqua;
  border-radius: 10px;
}
::-webkit-scrollbar-track{
  background-color: #182441
}
@media (max-width: 1280px) {
  .custom3{overflow-x: hidden;}
}
.animatePlanet1{
  animation: animationPlanet1 70s infinite linear
}
.animatePlanet2{
  animation: animationPlanet2 70s infinite linear
}
.animatePlanet3{
  animation: animationPlanet3 70s infinite linear
}
@keyframes animationPlanet1 {
  0%{
    transform: translate(20px,40px);
  }
  20%{
    transform: translate(1280px,250px);
  }
  40%{
    transform: translate(120px,300px);
  }
  60%{
    transform: translate(680px,550px);
  }
  80%{
    transform: translate(780px,50px);
  }
  100%{
    transform: translate(20px,40px);
  }
}
@keyframes animationPlanet2 {
  0%{
    transform: translate(1280px,680px);
  }
  20%{
    transform: translate(80px,50px);
  }
  40%{
    transform: translate(150px,300px);
  }
  60%{
    transform: translate(680px,550px);
  }
  80%{
    transform: translate(780px,50px);
  }
  100%{
    transform: translate(1280px,680px);
  }
}
@keyframes animationPlanet3 {
  0%{
    transform: translate(680px,300px);
  }
  20%{
    transform: translate(80px,50px);
  }
  40%{
    transform: translate(780px,600px);
  }
  60%{
    transform: translate(1280px,280px);
  }
  80%{
    transform: translate(80px,500px);
  }
  100%{
    transform: translate(680px,300px);
  }
}
